import React, { PureComponent } from 'react';
import Types from 'prop-types';

import Select from '../select';

import { explodeDate, convertToObjectDate, convertToISOStringDate } from './utils';
import { getMonthNames, isDateValid } from '../common/dateUtils';

import { DEFAULT_LOCALE } from './data';

const MonthBeforeDay = ['en-US', 'ja-JP'];
const DEFAULT_VALUES = { day: null, month: 0, year: null };

class DateInput extends PureComponent {
  static initializeDate = value => (isDateValid(value) ? explodeDate(value) : DEFAULT_VALUES);

  static returnValue = (_props, _state, value) => {
    let date = convertToISOStringDate(value);
    const previousReturned = convertToISOStringDate(_state.previousReturned);

    if (date !== previousReturned) {
      date = _state.format === 'iso' ? date : convertToObjectDate(value);
      _props.onChange(date);
    }
  };

  static propTypes = {
    disabled: Types.bool,
    size: Types.oneOf(['sm', 'md', 'lg']),
    value: Types.oneOfType([Types.string, Types.instanceOf(Date)]),
    locale: Types.string,
    onChange: Types.func.isRequired, // eslint-disable-line
    format: Types.oneOf(['iso', 'dateObject']),
  };

  static defaultProps = {
    disabled: false,
    size: 'md',
    value: null,
    locale: DEFAULT_LOCALE,
    format: 'dateObject',
  };

  constructor(props) {
    super(props);
    const { value, locale, format } = props;
    const { day, month, year } = DateInput.initializeDate(value);
    const date = convertToObjectDate(value);

    this.state = {
      day,
      month,
      year,
      value: date,
      previousReturned: date,
      monthBeforeDay: MonthBeforeDay.indexOf(locale) > -1,
      locale,
      format,
    };
  }

  static getDerivedStateFromProps(nextProps, state) {
    const value = convertToObjectDate(nextProps.value);

    if (convertToISOStringDate(value) !== convertToISOStringDate(state.value)) {
      if (isDateValid(value)) {
        DateInput.returnValue(nextProps, state, value);

        return {
          ...state,
          value,
          previousReturned: value,
          ...DateInput.initializeDate(value),
        };
      }

      if (!isDateValid(nextProps.value)) {
        DateInput.returnValue(nextProps, state, nextProps.value);

        return {
          ...state,
          value: null,
          previousReturned: null,
          ...DateInput.initializeDate(value),
        };
      }
    }
    if (nextProps.locale && nextProps.locale !== state.locale) {
      return {
        ...state,
        locale: nextProps.locale,
        monthBeforeDay: MonthBeforeDay.indexOf(nextProps.locale) > -1,
      };
    }

    if (nextProps.format && nextProps.format !== state.format) {
      return {
        ...state,
        format: nextProps.format,
      };
    }

    DateInput.returnValue(nextProps, state, value);

    return null;
  }

  getSelectElement = () => {
    const { disabled, size, locale } = this.props;
    const { month } = this.state;
    const months = getMonthNames(locale);
    return (
      <div>
        <label htmlFor="date-input-month" className="sr-only">
          Month
        </label>
        <Select
          id="date-input-month"
          name="month"
          className="form-control"
          selected={{ value: month, label: months[month] }}
          onChange={selectedValue => this.handleMonthChange(selectedValue)}
          disabled={disabled}
          options={this.getMonthsOptions()}
          size={size}
        />
      </div>
    );
  };

  getMonthsOptions = () => {
    const options = [];
    const { locale } = this.props;
    const months = getMonthNames(locale);

    months.forEach((month, index) => {
      options.push({ value: index, label: month });
    });
    return options;
  };

  handleDayChange = event => {
    const { month, year } = this.state;
    const { checkedDay } = this.checkDate(event.target.value, month, year);
    let value = new Date(year, month, checkedDay);
    value = isDateValid(value) ? value : null;

    this.setState({ day: checkedDay, value }, () => {
      DateInput.returnValue(this.props, this.state, value);
      // Prevents returning already returned values
      this.setState({ previousReturned: value });
    });
  };

  handleMonthChange = selectedValue => {
    const { day, year } = this.state;
    const month = selectedValue ? selectedValue.value : 0;
    const { checkedDay } = this.checkDate(day, month, year);
    let date = new Date(year, month, day);
    date = isDateValid(date) ? date : null;

    this.setState({ month, day: checkedDay, value: date }, () => {
      DateInput.returnValue(this.props, this.state, date);
      // Prevents returning already returned values
      this.setState({ previousReturned: date });
    });
  };

  handleYearChange = event => {
    const { value } = event.target;

    const slicedYear = value.length > 4 ? value.slice(0, 4) : value;

    if (slicedYear.length === 4) {
      // Correct day based on year and month.
      const { checkedDay, checkedYear } = this.checkDate(
        this.state.day,
        this.state.month,
        event.target.value,
      );
      let date = new Date(slicedYear, this.state.month, this.state.day);
      date = isDateValid(date) ? date : null;

      this.setState({ day: checkedDay, year: checkedYear, value: date }, () => {
        DateInput.returnValue(this.props, this.state, date);
        // Prevents returning already returned values
        this.setState({ previousReturned: date });
      });
    } else {
      this.setState({ year: slicedYear }, () => {
        let date = new Date(slicedYear, this.state.month, slicedYear);
        date = isDateValid(date) ? date : null;

        DateInput.returnValue(this.props, this.state, date);
        // Prevents returning already returned values
        this.setState({ previousReturned: date });
      });
    }
  };

  checkDate = (day = null, month = 0, year = null) => {
    let checkedDay = day;

    if (day && month) {
      const maxDay = new Date(year || 2000, month + 1, 0).getDate();
      checkedDay = day > maxDay ? maxDay : day;
    }
    if (day < 0) {
      checkedDay = 1;
    }
    if (day > 31) {
      checkedDay = 31;
    }

    return { checkedDay, checkedMonth: month, checkedYear: year };
  };

  render() {
    const { disabled, size } = this.props;
    const { day, year, monthBeforeDay } = this.state;

    return (
      <div className="tw-date">
        <div className="row">
          {monthBeforeDay && <div className="col-sm-5">{this.getSelectElement()}</div>}

          <div className="col-sm-3">
            <div className={`input-group-${size}`}>
              <label className="sr-only" htmlFor="date-input-day">
                Day
              </label>
              <input
                id="date-input-day"
                type="number"
                name="day"
                className="form-control"
                value={day || ''}
                onChange={event => this.handleDayChange(event)}
                placeholder="DD"
                disabled={disabled}
              />
            </div>
          </div>

          {!monthBeforeDay && <div className="col-sm-5">{this.getSelectElement()}</div>}

          <div className="col-sm-4">
            <div className={`input-group-${size}`}>
              <label className="sr-only" htmlFor="date-input-year">
                Year
              </label>
              <input
                id="date-input-year"
                type="text"
                name="year"
                className="form-control"
                placeholder="YYYY"
                value={year || ''}
                onChange={event => this.handleYearChange(event)}
                disabled={disabled}
              />
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default DateInput;



